

.miniscroller {
    width: 100vw;
    overflow: hidden;
    height: 494px;
}

.cards-slider-wrapper {
    display: flex;
    transition: transform 300ms ease-in;
    width: 80vw;
    margin: 0 3vw;
    height: 100%;
}

.card {
    flex: 1;
    opacity: 0.5;
    transform: scale(0.8);
    transition: opacity 300ms linear;     transition: transform 300ms ease-in;
}

.snippet-container {
    background: #363638;
    display: flex;
    padding-left: -5vw;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    text-align: center;
    width: 100%;
    height: 100%;
    padding: 1vh 1vw;
    box-shadow: 5px 7px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
}

.snippet-image {
    width: 100%;
    border: 1px solid #363638;
}

.snippet-header {
    margin: 1vh 1vw;
    color: white;
}

.snippet-description {
    color: white;
}

.hover {
    background: transparent;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 100%;
    top: 0;
}

.hover:hover {
    background: white;
    opacity: .85;
}

.hover:hover .snippet-button {
    display: block;
}

.snippet-button {
    color: #363638;
    display: none;
    font-size: 22px;
    text-decoration: none;
    padding: 16px 24px;
    margin: 3vh 0;
    border: 1px solid #363638;
}

.snippet-button:hover {
    color: white;
    background: #363638;

}


