.cards-slider {
    position: relative;
    max-width: 30vw;
    margin: 0 auto;
}

.cards-slider:after {
    content: '';
    display: block;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}


.arrow-right {
    position: relative;
    top: 8px;
    left: 8px;
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 10px solid #535d63;
    z-index: 30;
}

.arrow-left {
    position: relative;
    top: 8px;
    left: 8px;
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-right: 10px solid #535d63;
    z-index: 30;
}

.arrow-container {
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 0;
    display: inline-block;
    position: absolute;
    top: 8vw;
    cursor: pointer;
    border: 1px solid #535d63;
    z-index: 4;
    border-radius: 5px;
}

.arrow-container:hover {
    background: #535d63;
}

.arrow-container:hover .arrow-left {
    border-right: 10px solid white;
}

.arrow-container:hover .arrow-right {
    border-left: 10px solid white;
}

.right {
    right: -26vw;
}

.left {
    left: -26vw;
}

